import { useField } from 'formik'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { FormFeedback, Input, Label } from 'reactstrap'
import { InputType } from 'reactstrap/lib/Input'

import { useOrderModel } from '../hooks/useOrderModel'

interface IProps extends React.InputHTMLAttributes<HTMLInputElement> {
  name: string
  label?: string
  type?: InputType
  errorForceDisplay?: boolean
}

export const hiddenError = '<hidden>'

export const FormTextInput = observer<IProps>(
  ({
    name,
    label,
    type = 'text' as InputType,
    errorForceDisplay = false,
    ...element
  }) => {
    const orderModel = useOrderModel()
    const [field, meta] = useField(name, type)

    const labelNode = (
      <Label for={name} hidden={!label}>
        {element.placeholder || label}
      </Label>
    )

    const showError = errorForceDisplay || meta.touched

    return (
      <>
        {labelNode}
        <Input
          {...element}
          {...field}
          id={name}
          type={type}
          valid={meta.touched && !meta.error}
          invalid={showError && Boolean(meta.error)}
          disabled={orderModel.disableInteraction}
        />
        {showError && meta.error !== hiddenError ? (
          <FormFeedback valid={false} tooltip={false}>
            {meta.error}
          </FormFeedback>
        ) : null}
      </>
    )
  },
)
