import { useRouterContext } from '@speedlo/hooks'
import { useFormikContext } from 'formik'
import React from 'react'
import Spinner from 'react-spinkit'

import { PrimaryButton } from '../../common/Buttons'
import { OrderStateCategoryEnum, useQOrderForm } from '../../graph/generated'
import { useOrderModel } from '../../hooks/useOrderModel'

interface IProps {
  children: ReactNode
}

export const OrderSubmitButton: React.FC<IProps> = ({ children }) => {
  const formik = useFormikContext()
  const { history } = useRouterContext()
  const orderModel = useOrderModel()

  const { data, loading } = useQOrderForm(
    { orderId: orderModel.orderId! },
    { skip: !orderModel.orderId },
  )

  React.useEffect(() => {
    if (!orderModel.hasOrder || loading) {
      return
    }
    // ensure order is still in cart, otherwise redirect user away
    if (data.order.orderStateCategory !== OrderStateCategoryEnum.InCart) {
      history.replace('/')
    }
  })

  const canSubmit =
    !loading && data.order.countWares > 0 && orderModel.isVamInitialized

  return (
    <>
      <PrimaryButton
        type="submit"
        color="primary"
        size="md"
        disabled={!canSubmit || formik.isSubmitting}
        data-testid="OrderSubmitButton"
      >
        {formik.isSubmitting ? (
          <Spinner name="circle" fadeIn="none" color="#ccc" />
        ) : (
          children
        )}
      </PrimaryButton>
    </>
  )
}
