import { Trans } from '@lingui/macro'
import { useAutorun, useDebounceCallback } from '@speedlo/hooks'
import { useVamAgreements } from '@speedlo/ui'
import { useFormikContext } from 'formik'
import React from 'react'

import { useQOrderFormAgreements } from '../../graph/generated'
import { useOrderModel } from '../../hooks/useOrderModel'
import { OrderAgreementBox } from './OrderAgreementBox'
import { OrderFormValues, TAgreement } from './OrderFormSetup'

const EMPTY_ARRAY = []

export const OrderAgreements: React.FC = () => {
  const orderModel = useOrderModel()
  const formik = useFormikContext<OrderFormValues>()
  const [variables, setVariables] = React.useState(extractVariables)
  const updateVariablesLater = useDebounceCallback(setVariables, 2000)

  function extractVariables() {
    const { firstname, lastname, phonePrefix, phone } = formik.values
    return {
      input: {
        orderId: orderModel.orderId!,
        firstname,
        lastname,
        phone: `${phonePrefix.trim()}${phone.trim()}`,
      },
    }
  }

  React.useEffect(() => {
    updateVariablesLater(extractVariables())
    return () => updateVariablesLater.clear()
  }, [formik.values])

  const { data, errors, loading } = useQOrderFormAgreements(variables)

  // remember latest agreements to prevent rendering to blank on update of form values
  const agreements = React.useRef<RoA<TAgreement>>(EMPTY_ARRAY)
  const hasFailed = errors && errors.length > 0

  if (!(loading || hasFailed)) {
    agreements.current = data.agreements
  }

  React.useEffect(() => {
    if (agreements.current.length > 0) {
      orderModel.initVam()
    }
  })

  const { state, renderVamAgreements } = useVamAgreements(agreements.current)

  useAutorun(() => {
    // confirmed agreements are passed to formik to be collected for a submission later
    formik.setFieldValue('agreements', Array.from(state.confirmedAgreements))
  })

  // setup validation for mandatory agreements
  React.useEffect(() => {
    formik.registerField('agreements', {
      validate: () => {
        const result = agreements.current.some(agreement => {
          return (
            agreement.isMandatory &&
            !state.confirmedAgreements.has(agreement.id)
          )
        })
        return result ? 'mandatory' : ''
      },
    })
    return () => formik.unregisterField('agreements')
  }, [])

  if (hasFailed) {
    return (
      <Trans>
        Failed retrieving company agreements. Please try again later or contact
        us.
      </Trans>
    )
  }

  return renderVamAgreements({ AgreementBox: OrderAgreementBox })
}
