import { fakerCZ } from '@speedlo/testing/dist/faker'
import { useFormikContext } from 'formik'
import React from 'react'

import { SecondaryButton } from '../../common/Buttons'
import { useOrderModel } from '../../hooks/useOrderModel'
import { NOTE_MAX_LENGTH, OrderFormValues } from './OrderFormSetup'

export const OrderFormAutoFill: React.FC = () => {
  const orderModel = useOrderModel()
  const formik = useFormikContext<OrderFormValues>()
  if (process.env.REACT_APP_STAGE === 'prod') {
    return null
  }
  return (
    <SecondaryButton
      style={{ marginBottom: '0.5rem' }}
      onClick={() => {
        formik.setTouched({
          firstname: true,
          lastname: true,
          email: true,
          phone: true,
          phonePrefix: true,
          note: true,
        })
        formik.setValues({
          firstname: fakerCZ.name.firstName(),
          lastname: fakerCZ.name.lastName(),
          email: fakerCZ.internet.email(),
          phone: fakerCZ.phone.phoneNumberFormat(0).replace(/\s/g, ''),
          phonePrefix: '+420',
          note: fakerCZ.lorem.sentence().slice(0, NOTE_MAX_LENGTH - 1),
          agreements: [],
        })
        if (orderModel.usingPaymentMethodWithTips) {
          orderModel.setTips(15)
        }
      }}
    >
      Autofill form [DEV ONLY]
    </SecondaryButton>
  )
}
